import React, { Component } from 'react';
import { connect } from 'react-redux';
import './Courses.scss';
import Slider from "react-slick";
import "slick-carousel/slick/slick.css";
import "slick-carousel/slick/slick-theme.css";


class Courses extends Component {

    render() {
        let settings = {
            dots: false,
            infinite: true,
            speed: 500,
            slidesToShow: 5,
            slidesToScroll: 2
        };

        return (
            <div className="section-course">

                <div className="course-content">
                    <div className="court-header">
                        <span className="title-court"> LP HC NI BT</span>
                        <button className="button-court"> Xem thm </button>
                    </div>

                    <Slider {...settings}>
                        <div className="img-customize1">
                            <div className="img-court1" /> 
                            <div className='position'>LP HC HNG NGA</div>
                        </div>
                        <div className="img-customize2">
                            <div className="img-court2" /> 
                            <div className='position'> VIT BADMINTON</div>
                        </div>
                        <div className="img-customize3">
                            <div className="img-court3" /> 
                            <div className='position'>LP HC LC AN</div>
                        </div>
                        <div className="img-customize4">
                            <div className="img-court4" /> 
                            <div className='position'>LP HC NHTT</div>
                        </div>
                        <div className="img-customize5">
                            <div className="img-court5" /> 
                            <div className='position'>LP HC H - BNH THNH</div>
                        </div>
                        <div className="img-customize6">
                            <div className="img-court6" /> 
                            <div className='position'>LP HC QUN 1</div>
                        </div>
                    </Slider>

                </div>
            </div>

        );
    }

}

const mapStateToProps = state => {
    return {
        isLoggedIn: state.user.isLoggedIn
    };
};

const mapDispatchToProps = dispatch => {
    return {
    };
};

export default connect(mapStateToProps, mapDispatchToProps)(Courses);
